import Link from "next/link";
import Image from "next/image";
import SectionHeading from "@/src/components/layouts/SectionHeading";

const grades = [
  { title: "Grade 6", image: "/images/grade6.png", href: "/grades/6" },
  { title: "Grade 7", image: "/images/grade7.png", href: "/grades/7" },
  { title: "Grade 8", image: "/images/grade8.png", href: "/grades/8" },
  { title: "Grade 9", image: "/images/grade9.png", href: "/grades/9" },
  { title: "Grade 10", image: "/images/grade10.png", href: "/grades/10" },
  { title: "Grade 11", image: "/images/grade11.png", href: "/grades/11" },
  { title: "A/L ICT", image: "/images/al-ict.png", href: "/grades/al-ict" },
];

const MainCategoryHero = () => {
  return (
    <section className="py-8">
      <SectionHeading
        title="Exam Papers Collection"
        subtitle="Practice papers for Science, Mathematics, English, and ICT (Grades 6–11) plus Advanced Level ICT"
        align="center"
      />

      <div className="grid gap-8 lg:grid-cols-12 items-start">
        {/* LEFT SIDE – HERO IMAGE */}
        <div className="lg:col-span-4">
          <div className="relative rounded-2xl overflow-hidden shadow-lg h-120">

            <Image
              src="/images/grade6.png"
              alt="Exam Papers"
              fill
              className="object-cover"
              priority
            />
          </div>
        </div>

        <div className="lg:col-span-8">
          <div className="grid gap-6 sm:grid-cols-2 md:grid-cols-4">
            {grades.map((grade) => (
              <Link
                key={grade.title}
                href={grade.href}
                className="group rounded-2xl bg-white shadow-md hover:shadow-xl transition overflow-hidden"
              >
                <div className="relative h-44">
                  <Image
                    src={grade.image}
                    alt={grade.title}
                    fill
                    className="object-contain group-hover:scale-105 transition-transform duration-300"
                  />
                </div>

                <div className="p-4 text-center">
                  <h3 className="font-semibold text-gray-800">
                    {grade.title}
                  </h3>
                </div>
              </Link>
            ))}
          </div>
        </div>
      </div>
    </section>
  );
};

export default MainCategoryHero;
